import FacebookRoundedIcon from "@mui/icons-material/FacebookRounded";
import TelegramIcon from "@mui/icons-material/Telegram";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import { Button, Chip, Divider, Stack, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { useAuthContext } from "modules/login/context/authContext";
import { MainLayout } from "other/layouts/mainLayout";
import { toast } from "react-hot-toast";
import { useTranslation } from "react-i18next";
import QRCode from "react-qr-code";
import {
  FacebookShareButton,
  TelegramShareButton,
  WhatsappShareButton,
} from "react-share";
const GenerateQR = () => {
  const { userInfo } = useAuthContext();

  const { t } = useTranslation();
  const code = window.location.origin + `/main/donate/${userInfo?._id}`;
  const copy = async () => {
    await navigator.clipboard.writeText(code);
    toast.success(t("main.generateQR.copied"));
  };
  return (
    <MainLayout>
      <Box mb={2} width={1}>
        <Chip
          label={
            <Typography variant="h1">{t("main.generateQR.yourQR")}</Typography>
          }
        />
        <Divider />
      </Box>
      <Stack direction="column" justifyContent="center" alignItems="center">
        <Box width={300}>
          <MainLayout>
            <QRCode
              size={256}
              style={{ height: "auto", maxWidth: "100%", width: "100%" }}
              value={code}
              viewBox={`0 0 256 256`}
            />
          </MainLayout>
        </Box>
        <Stack
          direction={{ lg: "row", md: "row", sm: "row", xs: "column" }}
          alignItems="center"
          gap={3}
        >
          <Button onClick={copy}>{t("main.generateQR.copy")}</Button>

          <Stack direction="row" gap={1}>
            <FacebookShareButton url={`${code}`}>
              <Button>
                <FacebookRoundedIcon />
              </Button>
            </FacebookShareButton>
            <TelegramShareButton url={`${code}`}>
              <Button>
                <TelegramIcon />
              </Button>
            </TelegramShareButton>
            <WhatsappShareButton url={`${code}`}>
              <Button>
                <WhatsAppIcon />
              </Button>
            </WhatsappShareButton>
          </Stack>
        </Stack>
      </Stack>
    </MainLayout>
  );
};
export default GenerateQR;
